import PropTypes from "prop-types";
// @mui
import { alpha, styled } from "@mui/material/styles";
import { Typography, Button } from "@mui/material";
// components
import Iconify from "../../../../components/Iconify";
import useResponsive from "../../../../hooks/useResponsive";
import Image from "../../../../components/Image";
// ----------------------------------------------------------------------

const RootStyle = styled(Button)(({ theme }) => ({
  display: "block",
  textAlign: "center",
  boxShadow: theme.customShadows.z12,
}));

const IconWrapperStyle = styled("div")(({ theme }) => ({
  margin: "auto",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

// ----------------------------------------------------------------------

AnalyticsWidgetSummary.propTypes = {
  color: PropTypes.oneOf([
    "primary",
    "secondary",
    "info",
    "success",
    "warning",
    "error",
  ]),
  icon: PropTypes.string,
  iconWidth: PropTypes.number,
  iconHeight: PropTypes.number,
  title: PropTypes.string,
  active: PropTypes.bool,
  subtitle: PropTypes.string,
  handleClick: PropTypes.func,
};

export default function AnalyticsWidgetSummary({
  title,
  subtitle,
  icon,
  iconWidth = 150,
  iconHeight = 130,
  color = "primary",
  active,
  handleClick,
}) {
  const mdUp = useResponsive("between", "sm", "768", "1092");
  return (
    <RootStyle
      sx={{
        color: (theme) =>
          theme.palette.mode === "light"
            ? theme.palette[color].darker
            : "#ffffff",
        borderStyle: "solid",
        borderWidth: active ? 1.5 : 0,
        borderRadius: 2,
        borderColor: (theme) => theme.palette[color].dark,
        width: mdUp ? 3000 : 280,
        height: active ? (mdUp ? 280 : 260) : mdUp ? 260 : 240,
      }}
      onClick={handleClick}
    >
      <IconWrapperStyle sx={{ width: iconWidth, height: iconHeight }}>
        <img
          alt="Illustrator"
          src={`/static/assets/illustrations/${icon}.svg`}
        />
      </IconWrapperStyle>
      <Typography variant={active ? "h5" : "h6"}>{title}</Typography>
      <Typography
        variant={active ? "subtitle1" : "subtitle2"}
        sx={{ opacity: 0.72 }}
      >
        {subtitle}
      </Typography>
    </RootStyle>
  );
}
