import React, { useEffect, useState } from "react";
// @mui
import { m } from "framer-motion";
import { Container, Grid } from "@mui/material";
// hooks
import useAuth from "../../hooks/useAuth";
import useSettings from "../../hooks/useSettings";
// components
import Page from "../../components/Page";
// sections
import {
  AppMiniReport,
  AppMiniReport2,
  AppMiniReport3,
  TransactionStatistics,
  TransactionStatistics2,
  TransactionAgent,
  TransactionAgent2,
} from "../../sections/@dashboard/general/app";
import { MotionContainer, varFade } from "../../components/animate";
import handlePermission from "../../utils/handlePerm";
import {
  BookingIllustration,
  CheckInIllustration,
  CheckOutIllustration,
} from "../../assets";
import { getAppReport, getAppTicketReport } from "../../redux/slices/generale";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "../../redux/store";
// ----------------------------------------------------------------------

export default function GeneralApp() {
  const { user } = useAuth();
  const { t } = useTranslation();
  const { themeStretch } = useSettings();

  const [load_app_ticketreport, setLoad_app_ticketreport] = useState(false);

  const dispatch = useDispatch();

  const { appreport, appticketreport } = useSelector((state) => state.generale);

  useEffect(() => {
    dispatch(getAppReport());
  }, [dispatch]);

  const handleRefresh = async () => {
    setLoad_app_ticketreport(true);
    await dispatch(getAppTicketReport());
    setLoad_app_ticketreport(false);
  };

  return (
    <Page title="Home: Dashboard">
      <Container
        component={MotionContainer}
        maxWidth={themeStretch ? false : "xl"}
      >
        <Grid container spacing={2}>
          <Grid item xs={12} md={3}>
            <AppMiniReport
              t={t}
              bgColor="#FF9D23"
              title={t("app.total_balance")}
              total={appreport?.balance?.balance}
              currency={appreport?.balance?.currencyCode}
              percent={100}
              icon={<BookingIllustration />}
            />
          </Grid>
          <Grid item xs={12} md={3}>
            <AppMiniReport2
              t={t}
              bgColor="#3490DE"
              title={t("app.total_ticket")}
              total={appreport?.montants}
              total1={appreport?.tickets}
              percent={appreport?.percentage_progress?.montants}
              icon={<CheckInIllustration />}
            />
          </Grid>
          <Grid item xs={12} md={3}>
            <AppMiniReport3
              t={t}
              bgColor="#16C47F"
              title={t("app.total_vendor_actif_inactif")}
              total={appreport?.vendor_actifs}
              total1={appreport?.vendor_inactifs}
              percent={appreport?.percentage_progress?.vendor_actifs}
              icon={<CheckOutIllustration />}
            />
          </Grid>
          <Grid item xs={12} md={3}>
            <AppMiniReport
              t={t}
              bgColor="#8B5DFF"
              title={t("app.total_commission")}
              total={appreport?.commissions}
              percent={appreport?.percentage_progress?.commissions}
              icon={<BookingIllustration />}
            />
          </Grid>

          <Grid item xs={12} md={12}>
            <TransactionStatistics2
              t={t}
              appticketreport={appticketreport}
              handleRefresh={handleRefresh}
              load_app_ticketreport={load_app_ticketreport}
            />
          </Grid>
          {/* <Grid item xs={12} md={6}>
            <TransactionAgent
              title="Expenses"
              color="warning"
              icon={"eva:diagonal-arrow-right-up-fill"}
              percent={-0.5}
              total={8938}
              chartData={[111, 136, 76, 108, 74, 54, 57, 84]}
            />
          </Grid>
          <Grid item xs={12} md={6} lg={4}>
            <TransactionAgent2 />
          </Grid>

          <Grid item xs={12} md={6} lg={8}>
            <TransactionStatistics />
          </Grid> */}
        </Grid>
      </Container>
    </Page>
  );
}
