import PropTypes from "prop-types";
import React, { useEffect } from "react";
// @mui
import { alpha, styled, useTheme } from "@mui/material/styles";
import {
  Box,
  Stack,
  Card,
  Avatar,
  CardHeader,
  Typography,
} from "@mui/material";
// utils
import { fShortenNumber } from "../../../../utils/formatNumber";
// _mock_
import { _appAuthors } from "../../../../_mock";
import { useTranslation } from "react-i18next";
// components
import Iconify from "../../../../components/Iconify";
import { useDispatch, useSelector } from "../../../../redux/store";
import { getBestScoreds } from "../../../../redux/slices/annonces";
// ----------------------------------------------------------------------

const IconWrapperStyle = styled("div")(({ theme }) => ({
  width: 40,
  height: 40,
  display: "flex",
  borderRadius: "50%",
  alignItems: "center",
  justifyContent: "center",
  color: theme.palette.primary.main,
  backgroundColor: alpha(theme.palette.primary.main, 0.08),
}));

// ----------------------------------------------------------------------

export default function AppTopAuthors() {
  const theme = useTheme();
  const dispatch = useDispatch();
  const { best_scores } = useSelector((state) => state.annonce);
  const { t } = useTranslation();

  useEffect(() => {
    dispatch(getBestScoreds());
  }, [dispatch]);

  return (
    <Card sx={{ backgroundColor: theme.palette.background.default }}>
      <CardHeader title={t("params.leaderboard")} />
      <Stack spacing={3} sx={{ p: 3 }}>
        {best_scores.map((scored, index) => (
          <AuthorItem key={scored.id} scored={scored} index={index} />
        ))}
      </Stack>
    </Card>
  );
}

// ----------------------------------------------------------------------

AuthorItem.propTypes = {
  author: PropTypes.shape({
    avatar: PropTypes.string,
    favourite: PropTypes.number,
    name: PropTypes.string,
  }),
  index: PropTypes.number,
};

function AuthorItem({ scored, index }) {
  return (
    <Stack direction="row" alignItems="center" spacing={2}>
      <Avatar alt={scored.name} src={scored.photo} />
      <Box sx={{ flexGrow: 1 }}>
        <Typography variant="subtitle2">{scored.username}</Typography>
        <Typography
          variant="caption"
          sx={{
            mt: 0.5,
            display: "flex",
            alignItems: "center",
            color: "text.secondary",
          }}
        >
          <Iconify
            icon={"eva:award-outline"}
            sx={{ width: 16, height: 16, mr: 0.5 }}
          />
          {scored.total_points} points
        </Typography>
      </Box>

      <IconWrapperStyle
        sx={{
          ...(index === 1 && {
            color: "info.main",
            bgcolor: (theme) => alpha(theme.palette.info.main, 0.08),
          }),
          ...(index === 2 && {
            color: "error.main",
            bgcolor: (theme) => alpha(theme.palette.error.main, 0.08),
          }),
        }}
      >
        <Iconify icon={"ant-design:trophy-filled"} width={20} height={20} />
      </IconWrapperStyle>
    </Stack>
  );
}
