import merge from "lodash/merge";
import { useState } from "react";
import ReactApexChart from "react-apexcharts";
// @mui
import { Card, CardHeader, Box, TextField } from "@mui/material";
//
import { BaseOptionChart } from "../../../../components/chart";
import { LoadingButton } from "@mui/lab";
import Iconify from "../../../../components/Iconify";

// ----------------------------------------------------------------------

export default function TransactionStatistics2({
  appticketreport,
  handleRefresh,
  load_app_ticketreport,
  t,
}) {
  const chartOptions = merge(BaseOptionChart(), {
    legend: { position: "top", horizontalAlign: "right" },
    xaxis: {
      categories: appticketreport?.month,
    },
  });

  return (
    <Card>
      <CardHeader
        title={t("app.ticket_historic")}
        subheader={t("app.ticket_historic_detail")}
        action={
          <LoadingButton
            onClick={handleRefresh}
            loading={load_app_ticketreport}
            variant="contained"
            startIcon={
              <Iconify icon="eva:refresh-fill" width={20} height={20} />
            }
          >
            Refresh
          </LoadingButton>
        }
      />

      <Box sx={{ mt: 3, mx: 3 }} dir="ltr">
        {appticketreport && (
          <ReactApexChart
            type="area"
            series={appticketreport?.data}
            options={chartOptions}
            height={364}
          />
        )}
      </Box>
    </Card>
  );
}
