import React, { useEffect } from "react";
import PropTypes from "prop-types";
import Slider from "react-slick";
import { Link as RouterLink } from "react-router-dom";
import { useTranslation } from "react-i18next";
// @mui
import { alpha, useTheme, styled } from "@mui/material/styles";
import { Box, Card, Button, CardContent, Typography } from "@mui/material";
// components
import Image from "../../../../components/Image";
import { CarouselDots } from "../../../../components/carousel";
import { useDispatch, useSelector } from "../../../../redux/store";
import { getAnnonces } from "../../../../redux/slices/annonces";
import useResponsive from "../../../../hooks/useResponsive";

// ----------------------------------------------------------------------

const OverlayStyle = styled("div")(({ theme }) => ({
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  zIndex: 8,
  position: "absolute",
  backgroundColor: alpha(theme.palette.primary.dark, 0.3),
}));

// ----------------------------------------------------------------------

export default function AppAnnonce() {
  const theme = useTheme();

  const dispatch = useDispatch();
  const { annonces } = useSelector((state) => state.annonce);

  useEffect(() => {
    dispatch(getAnnonces());
  }, [dispatch]);

  const settings = {
    speed: 5000,
    dots: true,
    arrows: false,
    autoplay: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    rtl: Boolean(theme.direction === "rtl"),
    ...CarouselDots({ position: "absolute", right: 24, bottom: 24 }),
  };

  return (
    <Card>
      <Slider {...settings}>
        {annonces.map((item) => (
          <CarouselItem key={item.id} item={item} />
        ))}
      </Slider>
    </Card>
  );
}

// ----------------------------------------------------------------------

CarouselItem.propTypes = {
  item: PropTypes.shape({
    image: PropTypes.string,
    title: PropTypes.string,
    label: PropTypes.string,
    button: PropTypes.string,
    btn_link: PropTypes.string,
    is_target: PropTypes.bool,
  }),
};

function CarouselItem({ item }) {
  const { image, title, description, label, button, btn_link, is_target } =
    item;
  const { t } = useTranslation();
  const isDesktop = useResponsive("up", "md");

  return (
    <Box sx={{ position: "relative" }}>
      <CardContent
        sx={{
          left: isDesktop ? 15 : 0,
          bottom: 15,
          zIndex: 9,
          maxWidth: "80%",
          position: "absolute",
          color: "common.white",
        }}
      >
        {isDesktop && (
          <Typography variant="overline" sx={{ opacity: 0.48 }}>
            {label}
          </Typography>
        )}
        <Typography noWrap variant={isDesktop ? "h2" : "h3"} sx={{ mt: 1 }}>
          {title}
        </Typography>
        <Typography
          variant={isDesktop ? "subtitle" : "subtitle2"}
          sx={{ mt: 1 }}
        >
          {description}
        </Typography>
        <Box sx={{ mt: 2 }}>
          {!is_target ? (
            <Button to={btn_link} variant="contained" component={RouterLink}>
              {button}
            </Button>
          ) : (
            <Button
              href={btn_link}
              target="_blank"
              rel="noopener"
              variant="contained"
            >
              {button}
            </Button>
          )}
        </Box>
      </CardContent>
      <OverlayStyle />
      <Image alt={title} src={image} sx={{ height: { xs: 280, xl: 320 } }} />
    </Box>
  );
}
