import PropTypes from "prop-types";
// @mui
import { alpha, styled } from "@mui/material/styles";
import { Card, Typography, Box, Stack } from "@mui/material";
// utils
import { fNumber, fPercent } from "../../../../utils/formatNumber";
import Iconify from "../../../../components/Iconify";
// ----------------------------------------------------------------------

const IconWrapperStyle = styled("div")(({ theme }) => ({
  width: 24,
  height: 24,
  display: "flex",
  borderRadius: "50%",
  alignItems: "center",
  justifyContent: "center",
  marginRight: theme.spacing(1),
  color: theme.palette.success.main,
  backgroundColor: alpha(theme.palette.success.main, 0.16),
}));

const RootStyle = styled(Card)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
  padding: theme.spacing(2, 2, 2, 2),
  borderRadius: 2
}));

// ----------------------------------------------------------------------

BookingWidgetSummary.propTypes = {
  t: PropTypes.any,
  title: PropTypes.string,
  percent: PropTypes.number,
  bgColor: PropTypes.string,
  total: PropTypes.number,
  total1: PropTypes.number,
};

export default function BookingWidgetSummary({
  title,
  total,
  total1,
  bgColor,
  percent,
  t,
}) {
  return (
    <RootStyle sx={{ backgroundColor: bgColor, color: "white" }}>
      <Box sx={{ flexGrow: 1 }}>
        <Typography variant="subtitle2">{title}</Typography>
        <Typography variant="h3">{total} | {total1}</Typography>
        <Stack direction="row" alignItems="center">
          <IconWrapperStyle
            sx={{
              ...(percent < 0 && {
                color: "error.main",
                bgcolor: (theme) => alpha(theme.palette.error.main, 0.16),
              }),
            }}
          >
            <Iconify
              width={16}
              height={16}
              icon={
                percent >= 0 ? "eva:trending-up-fill" : "eva:trending-down-fill"
              }
            />
          </IconWrapperStyle>

          <Typography variant="subtitle2" component="span">
            {percent > 0 && "+"}
            {fPercent(percent)}
          </Typography>
          <Typography
            variant="body2"
            component="span"
            noWrap
            // sx={{ color: "text.secondary" }}
          >
            &nbsp;{t("app.than_yesterday")}
          </Typography>
        </Stack>
      </Box>
    </RootStyle>
  );
}
